import React from 'react';

export function DrawResult(props) {
    let result = props.state.result;
    let dop = Array(result.length).fill(0);
    if(typeof result === 'object') {
        return <div className='result'>
            <div>
                {dop.map((a,i) =>
                    <div>
                        {result[i].map(e => <input type='text' value={e.toFixed(3)} key={e}/>)}
                    </div>)
                }
            </div>
        </div>
    }
    else return <div className='result'><input type='text' value={result}/></div>
}
export let count = 0;
export function DrawMatrix(props) {
    let matrix = new Array(Number( props.state.row));
    let q = 0;
    for(let i=0;i<props.state.row;i++)
    {
        matrix[i]=q;
        q = q + Number(props.state.column);
    }
    count = q;
    let ar = new Array(Number( props.state.column));
    ar.fill(0);
    return <div className='matrix'>
        {matrix.map((e,key) => (
                <div key={key}>{
                    ar.map((a,i) =>
                        ( <input id={e + i} key={key + i} type='text'/>)
                    )}
                </div>
            )
        )}
    </div>
}
export function DrawMatrix1(props) {
    let matrix = new Array(Number( props.state.row));
    let q = count;
    for(let i=0;i<props.state.row;i++)
    {
        matrix[i] = q;
        q = q + Number(props.state.column);
    }
    let ar = new Array(Number( props.state.column));
    ar.fill(0);
    return <div className='matrix'>
        {matrix.map((e,key) => (
                <div key={key}>{
                    ar.map((a,i) =>
                        ( <input id={e + i} key={key + i} type='text'/>)
                    )}
                </div>
            )
        )}
    </div>
}
export function SelectSize(props) {
    return <select onChange={props.work}>
        <option>1</option>
        <option>2</option>
        <option>3</option>
        <option>4</option>
        <option>5</option>
        <option>6</option>
    </select>
}
export function Choise(props) {
    if(props.state.use === 'транспонирование' || props.state.use === 'ранг'){
        return(
            <div>
                <h2>Выберите количество строк<span className='red'>*</span></h2>
                <SelectSize work={props.row}/>
                <h2>Выберите количество столбцов<span className='red'>*</span></h2>
                <SelectSize work={props.column}/>
                <br/>
            </div>)
    }
    else {
        return(
            <div>
                <h2>Выберите количество строк и столбцов<span className='red'>*</span></h2>
                <SelectSize work={props.change}/>
            </div>
        )
    }
}

export function DopInfo() {
    return(
        <div>
            <p><b>Транспонирование матрицы</b> - это операция над матрицей, когда ее строки становятся столбцами с теми же номерами.</p>
            <p><b>Определитель матрицы</b> или <b>детерминант матрицы</b> - это одна из основных численных характеристик квадратной матрицы, применяемая при решении многих задач. Он «определяет» свойства матрицы.</p>
            <p><b>Рангом системы строк</b> называется максимальное количество линейно независимых строк этой системы.</p>
            <p><b>Союзная матрица</b> — матрица, составленная из алгебраических дополнений для соответствующих элементов транспонированной матрицы. </p>
            <p><b>Квадратная матрица</b> A^(-1) называется обратной к невырожденной матрице A, если A*A^(-1)=A^(-1)*A=E , где  E - это единичная матрица соответствующего порядка.</p>
        </div>
    )
}
export function DopInfo2() {
    return(
        <div>
            <p><b>Суммой матриц</b> A и B одного размера называется матрица C такого же размера, получаемая из исходных путем сложения соответствующих элементов</p>
            <p><b>Разностью матриц</b> A  и  B одного и того же размера называется матрица  C такого же размера, получаемая из исходных путем прибавления к матрице   матрицы  , умноженной на (-1).</p>
            <p><b>Произведением матрицы</b>  A(m*n) на матрицу B(n*k)  называется матрица C(m*k)  такая, что элемент матрицы C , стоящий в  i-ой строке и  j-ом столбце, т.е. элемент Cij , равен сумме произведений элементов  i-ой строки матрицы  A на соответствующие элементы  j-ого столбца матрицы B .</p>
        </div>
    )
}


